<template>
    <div class="cart">
        <div class="sub" @click="sub" >
            <img src="../../assets/imgs/sub.png" alt="" v-show="food.count">
        </div>
        <div class="count" v-show="food.count">
            {{food.count}}
        </div>
        <div class="add" @click="add">
            <img src="../../assets/imgs/add.png" alt="">
        </div>
    </div>
</template>
<script>
    import {mapGetters} from 'vuex'
    import Vue from 'vue'
    export default{
        props:[
            "foodList",
            "food"
        ],
        computed:{
            ...mapGetters([
                "goods",
                "seller"
            ])
        },
        methods:{
            add(){
                if(!this.food.count){
                    Vue.set(this.food,"count",1);
                }else{
                    this.food.count++;
                }
            },
            sub(){
                if(this.food.count>0){
                    this.food.count--;
                }
            }
        }
    }
</script>
<style lang="less" >
    .cart{
       
        width: 1.4rem;
        height: 0.4rem;
        margin-left: 0.1rem;
        position: relative;
        div:first-child{
            line-height: 0.4rem;
            text-align: center;
            top:0;
            left:0;
            height: 0.35rem;
            width: 0.35rem;
            font-size: 0.22rem;
            color:rgb(147,153,159);
            vertical-align: middle;
            display: inline-block;
            position: absolute;
            img{
                width: 0.35rem;
                height: 0.35rem;
            }
        }
        div:nth-child(2){
            line-height: 0.4rem;
            text-align: center;
            top:0;
            left:0.44rem;
            height: 0.35rem;
            width: 0.35rem;
            font-size: 0.22rem;
            color:rgb(147,153,159);
            vertical-align: middle;
            display: inline-block;
            position: absolute;
            img{
                width: 0.35rem;
                height: 0.35rem;
            }
        }
        div:last-child{
            line-height: 0.4rem;
            text-align: center;
            top:0;
            right:0;
            height: 0.35rem;
            width: 0.35rem;
            font-size: 0.22rem;
            color:rgb(147,153,159);
            vertical-align: middle;
            display: inline-block;
            position: absolute;
            img{
                width: 0.35rem;
                height: 0.35rem;
            }
        }
        
    }
</style>


