<template>
  <div class="rating" id="rating" ref="wrapper">
      <div>
        <!-- 综合评分 start -->
        <div class="foodScore">
          <!--sellerName start -->
          <div class="first">
              <div class="sellerName">
                <p>{{seller.name}}</p>
                <div class="starCount">
                   <star :score="2.6" :size="'small'"></star>
                    <div class="num">
                      ({{seller.score}})
                    </div>
                    <div class="sellCount">
                      月售{{seller.sellCount}}单
                    </div>
                </div>
              </div>
              <div class="like">
                <p><img src="../../assets/imgs/like.png" alt=""></p>
                <p>已收藏</p>
              </div>
          </div>
          
          <!-- sellerName end -->
          <!-- details start -->
          <div class="detail">
            <div class="one">
              <p>起送价</p>
              <div>
                <p>{{seller.minPrice}}</p>
                <div>元</div>
              </div>
            </div>
            <div class="one">
              <p>商家配送</p>
              <div>
                <p>{{seller.deliveryPrice}}</p>  
                <div>元</div>
              </div>
            </div>
            <div class="one">
              <p>平均配送时间</p>
              <div>
                <p> {{seller.deliveryTime}}</p> 
                <div>分钟</div>
              </div>
            </div>
          </div>
          <!-- details end -->
        </div>
        <!-- 综合评分 end -->
        <p class="myDiv"></p>
      
        <!-- comment start-->
        <div class="comment">
          <div class="commentDetail">
            <!-- active start -->
            <div class="action">
                <p>公告与活动</p>
                <div class="info">
                  {{seller.bulletin}}
                </div>
                <div class="discount info" v-if="seller.supports">
                  <div class="actDetail">
                    <img src="../../assets/imgs/decrease.png" alt="">
                    <div>{{seller.supports[0].description}}</div>
                  </div>
                  <div class="actDetail" v-if="seller.supports">
                    <img src="../../assets/imgs/discount.png" alt="">
                    <div>{{seller.supports[1].description}}</div>
                  </div>
                  <div class="actDetail" v-if="seller.supports">
                    <img src="../../assets/imgs/discount.png" alt="">
                    <div>{{seller.supports[2].description}}</div>
                  </div>
                  <div class="actDetail" v-if="seller.supports">
                    <img src="../../assets/imgs/invoice.png" alt="">
                    <div>{{seller.supports[3].description}}</div>
                  </div>
                  <div class="actDetail" v-if="seller.supports">
                    <img src="../../assets/imgs/guarantee.png" alt="">
                    <div>{{seller.supports[4].description}}</div>
                  </div>
                </div>
            </div>
            <!-- active end -->
          </div>
        </div>
        <!--comment end-->
        <!-- 空DIV start -->
        <p class="myDiv"></p>
        <!-- 空DIV end -->
        <!--  商户实景 start -->
        <div class="scene">
          <p>商家实景</p>
          <div class="sellerBg" >
            <div ref="bgframe" class="bgframe">
               <ul class="picList" ref="picList">
                <li v-for='(v,k) in seller.pics' :key="k" >
                  <img :src="v" alt="" >
                </li>
               </ul>
            </div>
           
            
          </div>
        </div>
        <!-- 空DIV start -->
        <p class="myDiv"></p>
        <!-- 空DIV end -->

        <!-- 商家信息 start -->
        <div class="comment">
          <div class="commentDetail">
            <!-- active start -->
            <div class="action">
                <p>&nbsp;&nbsp;商家信息</p>
                <div class="info"></div>
                <div class="discount info" v-if="seller.infos">
                  <div class="actDetail" v-for="(v,k) in seller.infos" :key="k">
                    <div>{{v}}</div>
                  </div>
                </div>
            </div>
            <!-- active end -->
          </div>
        </div>
        <!-- 商家信息 end -->
      </div>
      
      <!--  商户实景 end -->
      
      <bottom></bottom>
     
  </div>
</template>
<script>
import bottom from "../bottom/bottom.vue";
import star from "../star/star.vue";
import { mapGetters } from "vuex";
import BScroll from "better-scroll";

export default {
  components: {
    star,
    bottom
  },
  computed: {
    ...mapGetters(["seller"])
  },
  watch: {
    seller() {
      this.$nextTick( () => {
        this._initPicture();
      })
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {});
      this.scroll1 = new BScroll(this.$refs.bgframe, {});
    });
    this._initPicture();
  },
  methods:{
    _initPicture(){
      if(this.seller.pics){
        let picWidth = 2.4 ;
        let margin = 0.12;
        let width = (picWidth+margin)*this.seller.pics.length - margin;
        this.$refs.picList.style.width = width +"rem";
        if(!this.picBScroll){
						this.picBScroll = new BScroll( this.$refs.bgframe ,{
							scrollX : true ,
							eventPassthrough : 'vertical'
						})
					}else{
						this.picBScroll.refresh();
					}
      }
    }
  }
};
</script>

<style lang="less" scoped>
.rating {
  width: 100%;
  position: absolute;
  top: 3.44rem;
  bottom: 1rem;
  overflow: hidden;
  box-sizing: border-box;
  // foodScore
  .foodScore {
    width: 100%;
    height: 3rem;
    padding: 0.36rem;
    box-sizing: border-box;
    border-bottom: 0.02rem solid rgba(147, 153, 159, 0.2);
    .first {
      width: 100%;
      height: 1.45rem;
      display: flex;
      box-sizing: border-box;
      border-bottom: 1px solid rgba(7, 17, 27, 0.2);
      .sellerName {
        p {
          font-size: 0.28rem;
          color: rgb(7, 17, 27);
          line-height: 0.26rem;
          margin-bottom: 0.25rem;
        }
        .starCount {
          display: flex;
          .num {
            margin: auto 0.24rem auto 0.16rem;
          }
          .sellCount{
            margin-top:0.05rem;
          }
          
        }
      }
      .like {
        font-size: 0.2rem;
        color: rgb(77, 86, 93);
        line-height: 0.2rem;

        p {
          img {
            width: 0.42rem;
            height: 0.42rem;
            margin-bottom: 0.08rem;
          }
        }
        margin-left: 2.35rem;
      }
    }
    .detail {
      width: 100%;
      display: flex;
      justify-content: space-between;
      .one {
        margin-top: 0.36rem;
        width: 2.12rem;
        height: 1.45rem;
        //  border: 1px solid #000;
        text-align: center;

        p {
          font-size: 0.2rem;
          color: rgb(147, 153, 150);
          line-height: 0.2rem;
          margin-bottom: 0.08rem;
        }
        div {
          p {
            font-size: 0.26rem;
            font-weight: 200;
            color: rgb(7, 17, 27);
            line-height: 0.46rem;
          }
          div {
            position: relative;
            top: -0.4rem;
            left: 0.35rem;
          }
        }
      }
    }
  }
  // 空div
  .myDiv {
    width: 100%;
    height: 0.35rem;
    border-bottom: 0.02rem solid rgba(147, 153, 159, 0.2);
    box-sizing: border-box;
    background-color: rgba(147, 153, 159, 0.1);
  }
  .comment {
    width: 100%;
    // commentDetail
    .commentDetail {
      display: flex;
      margin: 0.36rem 0.36rem auto 0.36rem;
      // border: 0.01rem solid rgb(7, 17, 27);
      box-sizing: border-box;
      .action {
        border-bottom: 0.01rem solid rgba(7, 17, 27, 0.2);
        p {
          font-size: 0.28rem;
          font-weight: 600;
          margin-bottom: 0.16rem;
        }
        .info {
          margin: 0.24rem;
          font-size: 0.24rem;
          font-weight: 200;
          color: rgb(240, 20, 20);
          line-height: 0.4rem;
          border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        }
        .discount {
          .actDetail {
            display: flex;
            border-bottom: 1px solid rgba(7, 17, 27, 0.1);
            padding: 0.32rem;
            img {
              width: 0.34rem;
              height: 0.34rem;
              margin-right: 0.12rem;
            }
            div {
              font-size: 0.24rem;
              font-weight: 200;
              color: rgb(7, 17, 27);
              line-height: 0.32rem;
            }
          }
        }
      }
    }
  }
  .scene {
    width: 100%;
    height: 3rem;
    p {
      margin: 0.36rem auto 0.24rem 0.36rem;
      font-size: 0.28rem;
      color: rgb(7, 17, 27);
      line-height: 0.26rem;
      margin-bottom: 0.25rem;
    }
    .sellerBg {
      margin-left: 0.36rem;
      .bgframe {
        width: 6.6rem;
        white-space: nowrap;
        overflow: hidden;
        box-sizing: border-box;
        ul {
          display: flex;
          li {
            img {
             margin-right: 0.12rem;
             width: 2.4rem;
             height: 1.8rem;
            }
            &:last-child{
                margin:0;
              }
          }
        }
      }
    }
  }
  .bottom {
    position: fixed;
    bottom: 0;
    left:0;
    z-index: 10;
  }
}
</style>



