<template>
  <div class="goods">
    <!-- foodlist start -->
    <div ref="wrapper">
      <div class="foodlist">
        <ul>
          <li class="fooditem" v-for="(v,i) in goods" :key="i" @click='selectMenu(i)' :class="{'current':currentIndex == i}">
            <span class="text">
              {{v.name}}
            </span>
          </li>
        </ul>
      </div>
    </div>
    
    <!-- foodlist end -->
    
      <!-- food start -->
      <div class="foodwrap"  ref="foodwrap">
        <ul > 
          <li class="foodInfo food-list-hook" v-for="(item,k) in goods" :key="k">
            <div class="title">
              {{item.name}}
            </div>
            <ul>
              <li v-for='(food,k) in item.foods' class="itemDetail " :key="k">
                  <div class="showFood" >
                    <img :src="food.image" alt="" @click="showFooDdetail(food)">
                    <!-- 套餐详情 start-->
                    <div class="total">
                      <div @click="showFooDdetail(food)">{{food.name}}</div>
                      <div>{{food.description}}</div>
                      <div class="rate">
                        <p>月售出{{food.sellCount}}份</p>
                        <p>好评率{{food.rating}}%</p>
                      </div>
                      <div class="priceadd">
                        <p>¥ <span>{{food.price}}</span></p>
                        <p> ¥{{food.oldPrice}} </p>
                        <div class="shopcart">
                           <cart :food="food"></cart>
                        </div>
                       
                      </div>
                    </div>
                    <!-- 套餐详情 end-->
                  </div>
                </li>
            </ul>
          </li>
        </ul>
      </div>
      <!-- food end -->
    <foodDetail></foodDetail>
    <bottom></bottom>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  import BScroll from 'better-scroll'
  import bottom from '../bottom/bottom.vue'
  import cart from '../cart/cart.vue'
  import foodDetail from '../foodDetail/foodDetail.vue'
  export default{
    components :{
      bottom,
      cart,
      foodDetail
    },
      data(){
        return {
          scrollY:'',
          heights:[]
        }
      },
    computed:{
			...mapGetters([
				// 'getters',
				'goods'
      ]),
      currentIndex(){
        for(let i=0;i<this.heights.length;i++){
          let height1 = this.heights[i];
          let height2 = this.heights[i+1];
          if(this.scrollY >=height1 && this.scrollY < height2){
            return i;
          }
        }
        return 0;
      }
    },
    mounted(){
      this.$nextTick(()=>{
        this.initBScroll();
        this.calcHeight()
      })
    },
    methods:{
      showFooDdetail(f){
        this.$store.dispatch("showFooDdetail",f)
      },
      initBScroll(){
        // 右侧滚动 
        if(!this.foodwrapBScroll){
          this.foodwrapBScroll = new BScroll(this.$refs.foodwrap,{
            click:true,
            probeType:3
          })
        }else{
					this.foodlistBScroll.refresh();
        }
        // 左侧滚动
        if(!this.foodlistBScroll){
          this.foodlistBScroll = new BScroll(this.$refs.wrapper,{
            click:true
          })
        }else{
					this.foodlistBScroll.refresh();
        }
        //  右侧滑动获取Y坐标
        this.foodwrapBScroll.on('scroll',(pos)=>{
          this.scrollY = Math.abs(Math.round(pos.y));
          // console.log(this.scrollY);
        })

      },
      calcHeight(){
        let foodList = document.getElementsByClassName("food-list-hook");
        let height = 0;
        this.heights.push(height);
        for(var i=0;i<foodList.length;i++){
            height +=foodList[i].clientHeight;
            this.heights.push(height);
            
        }
      },
      selectMenu(i){
				console.log(i)
				let foodList = document.getElementsByClassName('food-list-hook');
				this.foodwrapBScroll.scrollToElement(foodList[i],300);
		  }

    }
  }
  
</script>
<style lang="less" scoped>
  .goods{
    display: flex;
    position: absolute;
    overflow: hidden;
    width: 100%;
    top: 3.5rem;
    bottom: 1rem;
    .bottom{
      position: fixed;
      bottom: 0;
      left: 0;
      z-index: 300;
    }
    //左边列表
    .foodlist{
      width: 1.6rem;
      flex: 0 0 1.6rem;
      ul{
        .fooditem{
          
          width: 1.12rem;
          height: 1.08rem;
          font-size: 0.24rem;
          background: #f3f5f7;
          color: rgb(1,17,27);
          line-height: 0.28rem;
          font-weight: 200;
          display: table;
          padding: 0 0.24rem;
          .text{
            display:table-cell;
            vertical-align: middle;
            border-bottom: 0.01rem solid rgba(7,17,27,0.1);
          }
        }
        .current{
            color:red;
        }
      }
    }
    /*右边套餐详情*/
    .foodwrap{
      box-sizing: border-box;
      flex:1;
     .foodInfo{
      
      .title{
         width: 100%;
         height: 0.5rem;
         line-height: 0.5rem;
         font-size: 0.24rem;
         color:rgb(147,153,159);
         background: rgba(7,17,27,0.1);
         padding-left:0.36rem;
         box-sizing: border-box;
       }
       ul{
          .itemDetail{
            padding:0.36rem;
            width: 100%;
						height: 2.2rem;
										
            line-height: 1rem;
            box-sizing: border-box;
            .showFood{
              display: flex;
              border-bottom: 1px solid rgba(7,17,27,0.1);	
              padding-bottom: 0.36rem;
              img{
                width: 1.14rem;
                height: 1.14rem;
                margin-right: 0.2rem;
                box-sizing: border-box;
              }
              /*套餐详情 */
              .total{
                height: 1.5rem;
                flex:1;
                // border: 1px solid #000;
                position: relative;
                div:first-child{
                    font-size: 0.28rem;
                    color: rgb(1,17,27);
                    line-height: 0.28rem;
                    font-weight: 200;
                    margin-bottom: 0.16rem;
                    box-sizing: border-box;
                    box-sizing: border-box;
                }
                div:nth-child(2){
                    font-size: 0.2rem;
                    color: rgb(147,153,159);
                    line-height: 0.2rem;
                    margin-bottom: 0.16rem;
                }
                .rate{
                  display: flex;
                  font-size: 0.2rem;
                  color: rgb(147,153,159);
                  line-height: 0.2rem;
                  margin-bottom: 0.16rem;
                  p:nth-child(2){
                    margin-left: 0.24rem;
                  }
                }
                .priceadd{
                  display: flex;
                  line-height: 0.2rem;
                  margin-bottom: 0.16rem;
                  position: relative;
                  .shopcart{
                    position: absolute;
                    right: 0;
                    top:0;
                  }
                  p:first-child{
                    font-size: 0.2rem;
                    color: red;
                    span{
                      font-size: 0.28rem;
                      font-weight: 700;
                      line-height: 0.48rem;
                      margin-right: 0.16rem;
                    }
                  }
                  p:nth-child(2){
                    font-size: 0.20rem;
                    color: rgb(147,163,159);
                    font-weight: 700;
                    line-height: 0.48rem;
                    text-decoration: line-through;
                  }
                  img{
                    
                    width: 0.4rem;
                    height: 0.4rem;
                    margin-right: 0rem;
                  }
                }
              }

            }
          }
       }
       
     }

    }
  }

</style>




