<template>
  <div class="rating" ref="wrapper">
    <div id="wrapper">
      <!-- 综合评分 start -->
      <div class="foodScore">
        <!-- bullentin start -->
        <div class="score">
            <p id="foodScore">{{seller.foodScore}}</p>
            <p id="totalScore">综合评分</p>
            <p id="rankRate">高于周边商家{{seller.rankRate}}%</p>
        </div>
        <!-- bullentin end -->
        <!-- serverScore start -->
        <div class="serverScore">
          <div class="ScoreDetail">
            <p>服务态度</p>
            <star :score="seller.foodScore" :size="'small'"></star>
            <div>{{seller.foodScore}}</div>
          </div>
          <div class="ScoreDetail">
            <p> 服务态度</p>
            <star :score="seller.serviceScore" :size="'small'"></star>
            <div>{{seller.serviceScore}}</div>
          </div>
          <div class="ScoreDetail">
            <P>送达时间</P>
            <div class="time">{{seller.deliveryTime}}分钟</div>  
          </div>
        </div>
          <!-- serverScore end -->
        
      </div>
      <!-- 综合评分 end -->
      <p class="myDiv"></p>
      <!-- button start-->
      <Buttons></Buttons>
      <!-- button end-->
      <!-- comment start-->
      <div class="comment">
        <div class="commentDetail" v-for="(v,i) in selectRatings" :key="i" >
          <div class="userImg">
            <img :src="v.avatar" alt="">
          </div>
          <!-- commentDetail start -->
          <div class="userInfo">
            <div class="infoTop">
              <p class="userName">{{v.username}}</p>
              <p class="commentDate">2017-10-9&nbsp;10:30:30</p>
            </div>
            <div class="starTime">
              <star :size="'small'" :score="v.score"></star>
              <p class="arriveTime">{{v.deliveryTime}}分钟到达</p>
            </div>
            
            <p class="commentContent">{{v.text}}</p>
            <div class="foodName" >
              <img src="../../assets/imgs/good.png" alt="">
              
              <div v-for="(item,k) in v.recommend" :key="k">{{item}}</div>
             
            </div>
          </div>
          
          <!-- commentDetail end -->
        </div>
        
      </div>
      <!--comment end-->
    </div>
		<bottom></bottom>
  </div>
</template>
<script>
import bottom from '../bottom/bottom.vue'
import star from "../star/star.vue"
import {mapGetters} from 'vuex'
import BScroll from 'better-scroll'
import Buttons from '../button/button.vue'
export default {
  components: {
    star,
    bottom,
    Buttons
  },
  computed:{
    ...mapGetters([
      "ratings",
      "seller",
      "selectRatings",
      "onlyContent"
    ])
  },
  mounted() {
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {
        click:true
      });
    });
    
  }
  
}
</script>

<style lang="less" scoped>
.rating {
  width: 100%;
  position: absolute;
  top: 3.44rem;
  bottom: 1rem;
  overflow: hidden;
  box-sizing: border-box;
  .bottom{
    position: fixed;
    bottom: 0;
    left:0;
  }

  // foodScore
  .foodScore {
    width: 100%;
    height: 2.13rem;
    display: flex;
    border-bottom: 0.02rem solid rgba(147, 153, 159, 0.2);
    .score {
      height: 100%;
      width: 2.75rem;
      text-align: center;
      #foodScore {
        font-size: 0.48rem;
        color: rgb(255, 153, 0);
        line-height: 0.56rem;
        margin: 0.36rem auto 0.17rem;
        box-sizing: border-box;
      }
      #totalScore {
        font-size: 0.24rem;
        color: rgb(7, 17, 27);
        line-height: 0.24rem;
        margin-bottom: 0.16rem;
        font-weight: 500;
        box-sizing: border-box;
      }
      #rankRate {
        font-size: 0.2rem;
        color: rgb(147, 153, 159);
        line-height: 0.2rem;
        box-sizing: border-box;
      }
    }
    .serverScore {
      width: 4.74rem;
      height: 1.4rem;
      margin-top: 0.36rem;
      padding-left: 0.48rem;
      box-sizing: border-box;
      border-left: 1px solid rgba(147, 153, 159, 0.2);
      .ScoreDetail {
        display: flex;
        font-size: 0.24rem;
        color: rgb(7, 17, 27);
        line-height: 0.36rem;
        box-sizing: border-box;
        p {
          margin-right: 0.15rem;
          margin-bottom: 0.16rem;
          box-sizing: border-box;
        }
        div {
          margin-left: 0.24rem;
          font-size: 0.28rem;
          color: rgb(255, 150, 0);
          line-height: 0.36rem;
          font-weight: 500;
          box-sizing: border-box;
        }
        .time {
          color: rgb(147, 153, 159);
        }
      }
    }
  }
  // 空div
  .myDiv {
    width: 100%;
    height: 0.35rem;
    border-bottom: 0.02rem solid rgba(147, 153, 159, 0.2);
    box-sizing: border-box;
    background-color: rgba(147, 153, 159, 0.1);
  }
  
  .comment {
		width: 100%;
    // commentDetail
    .commentDetail {
      display: flex;
      margin: 0.36rem 0.36rem auto 0.36rem;
      border-bottom: 0.01rem solid rgba(7,17,27,0.2);
      box-sizing: border-box;
      height: 2.24rem;
      position:relative;
      .userImg {
        img {
          width: 0.56rem;
          height: 0.56rem;
          border-radius: 0.5rem;
        }
        margin-right: 0.24rem;
        box-sizing: border-box;
      }
      // userInfo star
      .userInfo {
        .infoTop {
					display: flex;
					margin-bottom: 0.08rem;
          .userName {
            font-size: 0.2rem;
            color: rgb(7, 17, 27);
            line-height: 0.24rem;
            font-weight: 200;
          }
          .commentDate {
            font-size: 0.2rem;
            position:absolute;
            color: rgb(147, 153, 159);
            line-height: 0.24rem;
            font-weight: 200;
            margin-left:3.5rem;
          }
        }
				// star
				.starTime{
					display: flex;
					margin-bottom: 0.12rem;
					.star {
						img {
							width: 0.2rem;
							height: 0.2rem;
						}
					}
					.arriveTime {
						font-size: 0.2rem;
						color: rgb(147, 153, 159);
						line-height: 0.24rem;
						font-weight: 200;
						margin:0.1rem auto 0.12rem 0.12rem;
						margin-left:0.1rem;
						box-sizing: border-box;
					}
				}
        .commentContent {
					font-size: 0.24rem;
					color:rgb(7,17,27);
					// line-height: 0.36rem;
          width: 5.9rem;
          height: 0.6rem;
					overflow: hidden;
					
					text-overflow: ellipsis;
        }
			}
			.foodName{
        width:4.6rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        justify-content: space-around;
        // border:1px solid black;
				font-size: 0.18rem;
				color:rgb(147,153,159);
				img{
					width: 0.5rem;
					height: 0.4rem;
				}
				div{
          width: 1.25rem;
          display:inline-block;
					height: 0.33rem;
					border:0.01rem solid rgba(7,17,27,0.2);
					border-radius: 0.02rem;
					background-color: rgba(255,255,255,0.2);
          margin-top:0.05rem;
          margin-right:0.1rem;
					text-align: center;
					line-height: 0.33rem;
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;
				}
			}
    }
  }
}
</style>



