<template>
  <div class="bottom">
      <div class="bott" >
          <div class="shopCar">
              <div class="circle">
                  <div class="c2">
                      <div class="count" style="font-size:0.06rem;" v-if="totalCount>0">
                          {{totalCount}}
                      </div>
                     <img src="../../assets/imgs/shop-cart.png" alt="" @click="shopCart">
                  </div>
              </div>
              <div class="price">
                  ¥ {{totalPrice}}
              </div>
              <div class="deliveryprice">
                  另需配送费¥{{seller.deliveryPrice}}元
              </div>
          </div>
          
          <div class="total">
             {{payDesc}}
          </div>
      </div>
      <shopDetail :foodList="selectFoods"></shopDetail>
  </div>
</template>
<script>
    import {mapGetters} from 'vuex'
    import shopDetail from '../shopDetail/shopDetail.vue'
    export default{
        components:{
            shopDetail
        },
        computed:{
            ...mapGetters([
                "goods",
                "seller"
            ]),
        
            selectFoods(){
                let foods=[];
                this.goods.forEach((good)=>{
                    good.foods.forEach((food)=>{
                        if(food.count>0){
                            foods.push(food);
                        } 
                    })
                })
                return foods;
            },
            totalPrice(){
                let total = 0;
                this.selectFoods.forEach((food)=>{
                    total += food.count * food.price;
                })
                return total;
            },
            totalCount(){
                let count = 0;
                this.selectFoods.forEach((food)=>{
                    count += food.count;
                })
                return count;
            },
            payDesc(){
                if(this.totalPrice === 0){
                    return ` ¥${this.seller.minPrice}`
                }else if( this.totalPrice < this.seller.minPrice ){
                    return `还差￥${this.seller.minPrice - this.totalPrice}元起送`
                }else{
                    return `¥`+this.totalPrice+`去结算`;
                }
            }
            
        },
        methods:{
            shopCart(){
                this.$store.dispatch("shopCart")
            }
        }
    }
</script>
<style lang="less" scoped>
    .bottom{
        // position: fixed;
        // bottom:0;
        margin-top:0.1rem;
        .bott{
            display: flex;
            width: 100%;
            height: 0.96rem;
            .shopCar{
                width:5.4rem;
                background-color: rgb(19,29,38);
                .circle{
                    width: 1.16rem;
                    height: 1.16rem;
                    margin-top:-0.2rem;
                    background-color: rgb(19,29,38);
                    margin-left: 0.36rem;
                    border-radius: 1.16rem;
                    text-align: center;
                    position: relative;
                    .c2{
                        width: 0.88rem;
                        height: 0.88rem;
                        border-radius: 0.88rem;
                        background-color:rgba(43,51,59,0.9);
                        position: absolute;
                        top: 0.12rem;
                        left:0.13rem;
                        position: relative;
                        .count{
                            position: absolute;
                            top:0rem;
                            left:0.5rem;
                            width: 0.26rem;
                            height: 0.26rem;
                            border-radius: 0.26rem;
                            background-color: red;
                            color:white;
                            font-size: 0.02rem;
                            font-weight: 700;
                            text-align: center;
                            line-height: 0.26rem;
                        }
                        img{
                            width: 0.49rem;
                            height: 0.49rem;
                            vertical-align: middle;
                            margin-top:0.2rem;
                        }

                    }
                }
            
            }
            .price{
                position: absolute;
                width:1rem;
                height: 0.58rem;
                top:0.23rem;
                text-align: center;
                line-height: 0.58rem;
                left:1.6rem;
                border-right: 0.01rem solid rgba(255,255,255,0.4);
                color:rgba(255,255,255,0.5);
                font-size: 0.32rem;
                font-weight: 700;
            }
            .deliveryprice{
                position: absolute;
                width:2rem;
                height: 0.58rem;
                top:0.23rem;
                text-align: center;
                line-height: 0.58rem;
                left:2.8rem;
                color:rgba(255,255,255,0.5);
                font-size: 0.24rem;
                font-weight: 200;
            }
            .total{
                width: 2.12rem;
                height:0.96rem;
                background-color: rgba(43,51,59,0.9);
                text-align: center;
                line-height: 1rem;
                color:rgba(255,255,255,0.5);
                font-size: 0.24rem;
                font-weight: 500;
                
            }
        }
    }
</style>




