<template>
  <div id="app">
    <!-- header Start -->
    <topheader></topheader>
    <div class="tab">
      <div class="tabitem" >
        <router-link to="/goods">商品</router-link>
      </div>
      <div class="tabitem">
        <router-link to="/rating">评价</router-link>
      </div>
      <div class="tabitem">
        <router-link to="/seller">商家</router-link>
      </div>

    </div>

     <!-- header end -->
    <router-view/>
   
  </div> 
</template>

<script>
import topheader from './components/topheader/topheader.vue'

export default {
  name: 'app',
  components:{
    topheader
   
  },
  mounted(){
    this.$store.dispatch("getseller"),
    this.$store.dispatch("getGoods"),
    this.$store.dispatch("getRatings")
  }
}
</script>

<style lang="less" scoped>
   .tab{
     display: flex;
     font-size:0.28rem;
     font-weight: 200;
     line-height: 0.8rem;
     height: 0.8rem;
     width: 100%;
     box-sizing: border-box;
     border-bottom: 2px solid rgba(147,153,159,0.2);
     .tabitem{
       flex: 1;
       text-align: center;
     }
     a{
       display: block;
       &.active{
         color:rgb(240,20,20);
       }
     }
     
     
   }
</style>



