<template>
    <div class="Buttons">
        <div>
            <button type="button" @click="select(2)" :class="{'active':selectType === 2}">全部&nbsp;{{ratings.length}}</button>
            <button type="button" @click="select(0)" :class="{'active':selectType === 0}">满意&nbsp;{{goodCount}}</button>
            <button type="button" @click="select(1)" :class="{'active':selectType === 1}">不满意&nbsp;{{negativeCount}}</button>
        </div>
        <div @click="toggle" class='only' >
            <img src="../../assets/imgs/selected.png" alt="" v-show="!onlyContent">
            <img src="../../assets/imgs/select.png" alt="" v-show="onlyContent">
            <p :class="{'on':onlyContent}">只看有内容的评价</p>
        </div>
        
    </div>
</template>
<script>
    import {mapGetters} from "vuex"
    export default{
        computed:{
            ...mapGetters([
                "selectType",
                "types",
                "onlyContent",
                "selectRatings",
                "ratings"
            ]),
            goodCount(){
                return this.ratings.filter((rating)=>{
                    return rating.rateType == 0;
                }).length;
            },
            negativeCount(){
                 return this.ratings.filter((rating)=>{
                    return rating.rateType == 1;
                }).length;
            }
        },
        mounted(){
            // this.select();
            
            this.$nextTick( () => {
                this.$store.dispatch('changTypes',['推荐','吐槽','全部']);
            })
		
        },
        methods:{
            select(type){
                this.$store.dispatch("select",{type});
                // this.toggle();
            },
            toggle(){
                // console.log(5555);
                this.$store.dispatch("toggle");
            }
        }
    }   
</script>
<style lang="less" scoped>
    
.Buttons {
    width: 100%;
    height: 2.33rem;
    box-sizing: border-box;
    border-bottom: 0.02rem solid rgba(147, 153, 159, 0.2);
    padding: 0.36rem 0.35rem;
    div:first-child {
        width: 100%;
        border-bottom: 0.02rem solid rgba(147, 153, 159, 0.2);
        padding-bottom: 0.36rem;
        font-size: 0.24rem;
        button:first-child {
            height: 0.64rem;
            margin-right: 0.15rem;
            color: rgb(105, 111, 118);
            background-color: rgb(0, 160, 220);
            border: none;
        }
        button:nth-child(2) {
            height: 0.64rem;
            margin-right: 0.15rem;
            color: rgb(105, 111, 118);
            background-color: rgba(0, 160, 220,0.5);
            border: none;
        }
        button:last-child {
            height: 0.64rem;
            color: rgb(105, 111, 118);
            background-color: rgba(105, 111, 118,0.5);
            border: none;
        }
        button{
            outline: none;
            &.active{
                color:white;
            }
        }
    }
    div:nth-child(2) {
        box-sizing: border-box;
        margin-top: 0.3rem;
        display: flex;
        img {
            width: 0.4rem;
            height: 0.4rem;
        }
        p{
            &.on{
                color:rgb(0, 160, 220);
            }
            font-size: 0.26rem;
            color: rgb(147, 153, 159);
            margin: 0.09rem auto auto 0.17rem;
        }
        
        
    }
    
    
}
</style>



