<template>
    <div class="star" :class="starType">
        <span v-for="(itemClass,k) in itemClasses" :key="k" :class="itemClass" class="star-item"></span>
        
    </div>
</template>
<script>
    import {mapGetters} from "vuex"
    const LENGTH = 5;
    const ON = 'on';
    const OFF = 'off';
    const HALF = 'half'
    export default{
       computed:{
          starType(){
              return "star-"+this.size;
          },
          itemClasses(){
              let result = [];
              let integer = Math.floor(this.score);
              let decimal =  this.score - integer;
              for(let i=0;i<integer;i++){
                  result.push(ON);
                  }
              if(decimal>0.5){
                 result.push(HALF);
              }
              while( result.length < LENGTH ){
                result.push( OFF );
                // result[ON,ON,ON,HALF,OFF]
			  }
			  return result;
          }
       },
       props:[
           "score",
           "size"
      ]
    }
</script>
<style lang="less" scoped>
 .star{
     &.star-small{
         .star-item{
             display: inline-block;
             width: 0.3rem;
             height: 0.3rem;
             margin-right: 0.1rem;
             &.on{
                 background-image: url("../../assets/imgs/star_on.png");
                 background-size: 0.3rem 0.3rem;
                 background-repeat: no-repeat;
             }
             &.half{
                 background-image: url("../../assets/imgs/star_half.png");
                 background-size: 0.3rem 0.3rem;
                 background-repeat: no-repeat;
             }
             &.off{
                 background-image: url("../../assets/imgs/star_off.png");
                 background-size: 0.3rem 0.3rem;
                 background-repeat: no-repeat;
             }
         }
     }
 }
</style>




