import TYPE from '../types.js'
import axios from 'axios'
const state ={
    seller:{},
    sellerShow:false
}
const getters = {
    seller(state){
        return state.seller
    },
    sellerShow(state){
        return state.sellerShow;
    }
}
const actions = {
    getseller({commit}){
        axios.get("./data.json").then((res)=>{
            commit(TYPE.GET_SELLER,res.data.seller);
        })
    },
    showDetail({commit}){
        commit(TYPE.SHOW_DETAIL);
    },
    hideDetail({commit}){
        commit(TYPE.HIDE_DETAIL);
    }
}
const mutations ={
    [TYPE.GET_SELLER](state,data){
        // console.log(data);
        state.seller = data;
    },
    [TYPE.SHOW_DETAIL](state){
        state.sellerShow = true;
    },
    [TYPE.HIDE_DETAIL](state){
        state.sellerShow = false;
    }

}
export default {
    state,
    actions,
    mutations,
    getters
}


