import TYPES from "../types"
import axios from "axios"
const state = {
    goods:[],
    food:{},
    detailShow:false,
    selectFood:[],
    flag:false
}
const getters = {
    goods(state){
        return state.goods
    },
    detailShow(state){
        return state.detailShow;
    },
    food(state){
        return state.food;
    },
    selectFood(state){
        return state.selectFood
    },
    flag(state){
        return state.flag;
    }
}
const actions = {
    getGoods({commit}){
        axios.get("./data.json").then((res)=>{
            commit(TYPES.GET_GOODS,res.data.goods);
        })
    },
    showFooDdetail({commit},food){
       commit(TYPES.SHOW_FOODDETAIL,food)
    },
    exit({commit}){
        commit(TYPES.EXIT);
    },
    shopCart({commit}){
        commit(TYPES.SHOPCART)
    },
    doDelete({commit}){
        commit(TYPES.DODELETE)
    }
    
}
const mutations = {
    [TYPES.GET_GOODS](state,data){
        console.log(data);
		state.goods = data;
    },
    [TYPES.SHOW_FOODDETAIL](state,food){
        state.detailShow = true;
        state.food = food;
        this.state.rating.ratings = food.ratings;
        console.log(this.state.rating)
    },
    [TYPES.EXIT](state){
        state.detailShow = false;
    },
    [TYPES.SHOPCART](state){
        state.flag = !state.flag;
        console.log(1111)
    },
    [TYPES.DODELETE](state){
        state.flag = !state.flag;
        console.log(1111)
    }

}
export default {
    state,
    getters,
    actions,
    mutations
}


