<template>
 <transition enter-active-class="animated bounceInDown" leave-active-class="animated bounceOutUp">
     
     <div class="sellerDetail" v-show="sellerShow">
         <div class="bg"></div>
         <!-- 商家细节 start -->
         <div class="details">
            <div class="name">{{seller.name}}</div>
            <star></star>
            <div class="disInfo">
               <div class="line"></div>
               <div>优惠信息</div> 
               <div class="line"></div>
            </div>
            <!-- 优惠信息 start -->
            <div v-if="seller.supports" class="disDetails">
                <div>
                    <img src="../../assets/imgs/decrease.png" alt="">
                    <p>{{seller.supports[0].description}}</p>
                </div>
                <div>
                    <img src="../../assets/imgs/discount.png" alt="">
                    <p> {{seller.supports[1].description}}</p>  
                </div>
                <div>
                    <img src="../../assets/imgs/discount.png" alt="">
                    <p>{{seller.supports[2].description}}</p> 
                </div>
                <div>
                    <img src="../../assets/imgs/special.png" alt="">
                    <p>{{seller.supports[3].description}}</p>
                </div>
                <div>
                    <img src="../../assets/imgs/special.png" alt="">
                    <p>{{seller.supports[4].description}}</p>
                </div>
            </div>
            <!-- 优惠信息 end -->
            <!-- 商家公告start -->
            <div class="disInfo">
               <div class="line"></div>
               <div>商家公告</div> 
               <div class="line"></div>
            </div>
            <div class="bulletin">
              {{seller.bulletin}}  
            </div>
            <!-- 商家公告end -->
         <!--  exit button -->
         <div class="exit">
              <img src="../../assets/imgs/Exit.png" alt="" @click="hideDetail" id="exit">
         </div>
           
         </div>
          <!-- 商家细节 end -->
        
     </div>
    
 </transition>
</template>
<script>
    import {mapGetters} from "vuex"
    import animated from 'animate.css'
    import star from '../star/star.vue'
    export default{
        
        components:{
            star
        },
        computed:{
            ...mapGetters([
                "sellerShow",
                "seller"
            ])
        },
        methods:{
            hideDetail(){
                this.$store.dispatch("hideDetail")
            }
        }
    }
</script>
<style lang="less" scoped>
    .sellerDetail{
        width:100%;
        height: 100%;
        position: fixed;
        left:0;
        top: 0;
        box-sizing: border-box;
        .bg{
            width:100%;
            height: 100%;
            position: absolute;
            top:0;
            left: 0;
            z-index: -1rem;
            background-color: rgba(7,17,27,0.8);
            filter: blur(10px);
        }
        .details{
            position: absolute;
            z-index: 1rem;
            width:100%;
            height: 100%;
            box-sizing: border-box;
            padding: 1.28rem 0.72rem 0.64rem;
            
            .name{
                font-size: 0.32rem;
                font-weight: 700;
                color: white;
                line-height: 0.32rem;
                text-align: center;
            }
            .disInfo{
                display: flex;
                justify-content: space-between;
                font-size: 0.28rem;
                font-weight: 600;
                margin-top: 0.56rem;
                margin-bottom: 0.48rem;
                box-sizing: border-box;
                .line{
                    box-sizing: border-box;
                    border-radius: 0.02rem;
                    width: 2.24rem;
                    border-top:0.03rem solid rgba(255,255,255,0.2);
                    margin-top: 0.125rem;
                }
                
            }
            .disDetails{
                font-size: 0.24rem;
                font-weight: 200;
                color:rgb(255,255,255);
                line-height: 0.24rem;
                div{
                    display: flex;
                    img{
                        box-sizing: border-box;
                        margin:auto 0.12rem 0.24rem 0.24rem;
                        width: 0.32rem;
                        height: 0.32rem;
                        border-radius: 0.04rem;
                    }
                    p{
                        margin-top: 0.05rem;
                    }
                }
                
            }
            .bulletin{
                margin:0.48rem 0.24rem 1.93rem;
                box-sizing: border-box;
                font-size: 0.24rem;
                font-weight: 200;
                line-height: 0.48rem;
            }
            .exit{
                width: 100%;
                text-align: center;
               #exit{
                    width: 0.44rem;
                    height: 0.44rem;
                    margin:0 auto;
                    margin-bottom: 1.2rem;
                } 
            }
            
        }
    }
</style>




