<template>
    <div class="bg" v-show="flag" >
        <!-- <div></div> -->
        
        <div class="shopDetail" ref="wrapper">
            <div>
                <div class="top">
                    <div>购物车</div>
                    <div @click="doDelete">清空</div>
                </div>
                <div class="content" >
                    <ul>
                        <li v-for="(v,i) in foodList" :key="i">
                            <div class="foodName">
                                {{v.name}}
                            </div>
                            <div class="foodPrice">¥{{v.price}}</div>
                            <cart :food="v"></cart>
                        </li>
                        
                    </ul>
                    
                </div>
            </div>
            
        </div>
      
        
    </div>
  
</template>
<script>
    import {mapGetters} from 'vuex'
    import cart from "../cart/cart.vue"
    import BScroll from 'better-scroll'
    export default{
        props:[
            "foodList"
        ],
        computed:{
            ...mapGetters([
                "flag",
                "food",
                "goods",
                "selectFood"
            ])
        },
        components:{
            cart
        },
        mounted(){
            this.$nextTick(()=>{
                if(!this.foodBScroll){
                    this.foodBScroll = new BScroll(this.$refs.wrapper,{
                        click :true
                    })
                }else{
                   this.foodBScroll.refresh(); 
                }
                
            })
        },
        methods:{
            doDelete(){
                this.foodList.forEach((food)=>{
                    food.count = 0;
                })
                this.$store.dispatch("doDelete");
            }
        }
    }
</script>
<style lang="less">
    .bg{
        width:100%;
        height: 100%;
        position: fixed;
        left:0;
        top: 0;
        box-sizing: border-box;
        background-color: rgba(7,17,27,0.6);
        z-index: -1000;
       .shopDetail{
            width:100%;
            height: 50%;
            position: absolute;
            left:0;
            top: 43%;
            box-sizing: border-box;
            background-color: white;
            // z-index: -10;
                .top{
                    width: 100%;
                    height: 0.8rem;
                    background-color: #f3f5f7;
                    display: flex;
                    justify-content: space-between;
                    font-size: 0.28rem;
                    font-weight: 200;
                    color: rgb(7,17,27);
                    line-height: 0.8rem;
                    div:first-child{
                        margin-left: 0.36rem;
                    }
                    div:nth-child(2){
                        margin-right: 0.36rem;
                        color: rgb(0,160,220);
                    }
                    
                }
                .content{
                    // position: absolute;
                    // overflow: hidden;
                    // top:0;
                    // left: 0;
                    // height: 4.35rem;
                    width: 100%;
                    box-sizing: border-box;
                
                    ul{
                        margin-left:0.36rem;
                        margin-right:0.36rem;
                        
                        // z-index:-2;
                        li{
                            display: flex;
                            height: 0.96rem;
                            line-height: 0.96rem;
                            border-bottom: 0.01rem solid rgba(7,17,27,0.6);
                            box-sizing: border-box;
                            .foodName{
                                width: 4rem;
                                box-sizing: border-box;
                                font-size: 0.28rem;
                                color: rgb(7,17,27);
                                overflow: hidden;
                                white-space: nowrap;
                                text-overflow: ellipsis;
                            }
                            .foodPrice{
                                width: 0.5rem;
                                box-sizing: border-box;
                                font-size: 0.28rem;
                                color: rgb(240,20,20);
                                font-weight: 700;
                                margin-right: 0.24rem;
                            }
                            .cart{
                                margin-top:0.3rem;
                            }
                        }
                    }
                }
                
            }
       
    }
</style>




