<template>
    <!-- header start -->
  <div class="topheader">
      <img :src="seller.avatar" alt="" id="bg">
      <!-- 店家信息开始 -->
      <div class="top">
        <img :src="seller.avatar" alt="" @click="showDetail">
        <div class="sellerinfo">
            <img src="../../assets/imgs/brand.png" alt="">
            <span class="bandname">{{seller.name}}</span>
            <p class="deliveyTime">{{seller.description}}/{{seller.deliveryTime}}分钟送达</p>
            <p>
                <img src="../../assets/imgs/decrease.png" alt="" id="decrease">
                <span v-if="seller.supports" class="support">{{seller.supports[0].description}}</span>
                <div v-if="seller.supports" class="more">{{seller.supports.length}}个&nbsp;></div>
            </p>
        </div>
      </div> 
      <!-- 店家信息结束 -->
      <!-- 公告开始 -->
      <div class="bulletin">
          <img src="../../assets/imgs/bulletin.png" alt="">
          <div>{{seller.bulletin}}</div>
          <span>></span>
      </div>
      <!-- 公告结束 -->
      <!-- 商品详情 start-->
      <sellerDetail></sellerDetail>
       <!-- 商品详情 end-->
  </div>
   <!-- header end -->
</template>
<script>
    import { mapGetters} from "vuex"
    import sellerDetail from "../sellerDetail/sellerDetail.vue"
    export default{
        computed:{
            ...mapGetters([
                "seller"
                
            ])
        },
        components:{
            sellerDetail
        },
        methods:{
            showDetail(){
                this.$store.dispatch("showDetail")
            }
        }
    }
</script>
<style lang="less" scoped>
    .topheader{
        width: 100%;
        height: 2.68rem; 
        color:white;
        position: relative;
        box-sizing: border-box;
        overflow: hidden;
        #bg{
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top:0;
            z-index: -19;
            filter:blur(5px);
        }
        .top{
            background-color:rgba(1,17,27,0.5);
            width:100%;
            height:2.12rem;
            display: flex;
            box-sizing: border-box;
            padding:0.48rem 0.24rem 0.36rem 0.48rem;
            img{
                width: 1.28rem;
                height: 1.28rem;
                border-radius: 0.04rem;
                margin-right:0.32rem;
            }
            .sellerinfo{
                width:80%;
                box-sizing: border-box;
                line-height: 0.24rem;
                color:rgb(255,255,255);
               img:first-child{
                    position: relative;
                    top:0.04rem;
                    width:0.58rem;
                    height:0.37rem;
                    box-sizing: border-box;
                    margin-right:0.12rem;
                }
                .bandname{
                    font-size: 0.32rem;
                    font-weight: bold;
                    line-height: 36px;
                    margin-top:-0.1rem;
                    margin-bottom: 0.16rem;
                    box-sizing: border-box;
                    
                }
                .deliveyTime{
                    font-size: 0.24rem;
                    font-weight: 200;
                    box-sizing: border-box;
                }
                #decrease{
                    width:0.25rem;
                    height:0.25rem;
                    margin-right: 0.08rem;
                    margin-top: 0.1rem;
                    box-sizing: border-box;
                }
                .support{
                    font-size:0.2rem;
                }
                .more{
                    float: right;
                    box-sizing: border-box;
                    margin-top:-0.38rem;
                    width:0.9rem;
                    height: 0.5rem;
                    text-align: center;
                    font-size: 0.16rem;
                    font-weight: 200;
                    padding:0.14rem 0.16rem 0.14rem 0.16rem;
                    background-color:rgba(0,0,0,0.2);
                    border-radius: 0.3rem;
                }
            }
        }
        .bulletin{
            width: 100%;
            height: 0.56rem;
            background-color:rgba(7,17,27,0.65);
            box-sizing: border-box;
            img{
                width:0.5rem;
                height:0.25rem;
                margin:0.16rem 0.08rem 0.15rem 0.24rem;
                box-sizing: border-box;
            }
            div{
                width:6.24rem;
                top:-0.56rem;
                left:0.9rem;
                height: 0.56rem;
                position: relative;
                font-size: 0.2rem;
                font-weight: 200;
                line-height: 0.56rem;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
            span{
                top:-0.96rem;
                left:7.1rem;
                height: 0.56rem;
                position: relative;
                font-size: 0.24rem;
            }
        }
        .sellerDetail{
            z-index: 9999999;
        }
            
            
    }
    
</style>




