<template>
   <div class="foodDetail" ref="wrapper" v-show="detailShow">
    <div >
        <div class="foodImg" @click="exit">
            <img :src="food.image" alt="" >
        </div> 
        <div class="content">
            <div class="foodScore">
                <div class="first">
                    <div class="sellerName">
                        <p>{{seller.name}}</p>
                        <div class="starCount">
                            <div class="num">
                                月售{{seller.sellCount}}单
                            </div>
                            <div class="ratings">
                                好评100%
                            </div>
                        </div>
                    </div>
                    <div class="price">
                        <p>
                            ¥ <span>{{food.price}}</span>
                            <span> ¥{{food.oldPrice}}</span>
                        </p>
                        <div>
                            <cart :food="food"></cart>
                            <!-- <input type="button" value="加入购物车"> -->
                        </div>
                    </div>
                </div>        
            </div>
            <!-- 商品介绍 start -->
            <p class="myDiv"></p>
            <div class="foodScore">
                <div class="first">
                    <div class="sellerName">
                        <p>商品介绍</p>
                        <div class="introduce">
                            {{food.info}}
                        
                        </div>
                    </div>
                    
                </div> 
            <!-- 商品介绍 end -->       
            </div>
            <p class="myDiv"></p>
            <!-- button start-->
            <div class="goodrating">
                <div class="goodword">
                    商品评价
                </div>
                <Buttons></Buttons>
            <!-- button end-->
            <!-- comment start-->
                <div class="comment">
                    <div class="commentDetail" v-for="(v,i) in selectRatings" :key="i" >
                    <div class="userImg">
                        <img :src="v.avatar" alt="">
                    </div>
                    <!-- commentDetail start -->
                    <div class="userInfo">
                        <div class="infoTop">
                        <p class="userName">{{v.username}}</p>
                        <p class="commentDate">2017-10-9&nbsp;10:30:30</p>
                        </div>
                        <div class="starTime">
                        <star :size="'small'" :score="v.score"></star>
                        <p class="arriveTime">{{v.deliveryTime}}分钟到达</p>
                        </div>
                        
                        <p class="commentContent">{{v.text}}</p>
                        <div class="foodName" >
                        <img src="../../assets/imgs/good.png" alt="">
                        <div v-for="(item,k) in v.recommend" :key="k">{{item}}</div>
                        
                        </div>
                    </div>
                    
                    <!-- commentDetail end -->
                    </div>
                    
                </div>
            <!--comment end-->
        </div>      
        </div>
    </div>
   </div>
</template>
<script>
import { mapGetters } from "vuex"
import BScroll from 'better-scroll'
import star from '../star/star'
import cart from '../cart/cart.vue'
import Buttons from '../button/button.vue'
export default {
  computed: {
    ...mapGetters([
        "detailShow",
        "food",
        "seller",
        "ratings",
        "selectRatings",
        'goods'
    ])
  },
  components:{
     star,
     cart,
     Buttons
  },
  mounted(){
      this.$nextTick(()=>{
          if(!this.foodDetailScroll){
              this.foodDetailScroll = new BScroll(this.$refs.wrapper,{
                  click : true
              })
          }else{
              this.foodDetailScroll.refresh();
          }
      }),
      this.$store.dispatch("changTypes",['推荐','吐槽','全部']);
  },
  methods: {
    exit() {
      this.$store.dispatch("exit");
    }
  }
};
</script>
<style lang="less" scoped>
.foodDetail {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: white;
  .foodImg {
    width: 100%;
    position: relative;
    height: 0;
    padding-top: 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
    
  }
  .content{
    // foodScore
    .foodScore {
      width: 100%;
      height: 2.17rem;
      display: flex;
      border-bottom: 0.02rem solid rgba(147, 153, 159, 0.2);
      .first {
        width: 100%;
        height: 2.17rem; 
        box-sizing: border-box;
        // border: 1px solid #000;
        overflow: hidden;
        border-bottom: 1px solid rgba(7, 17, 27, 0.2);
        .price{
            display: flex;
            margin:0.28rem auto 0.16rem 0.36rem;
            position: relative;
            p:first-child{
                color: red;
                font-size: 0.2rem;
                font-weight: 700;
                span{
                    font-size: 0.28rem;
                    font-weight: 700;
                    line-height: 0.48rem;
                }
                span:nth-child(2){
                    font-size: 0.28rem;
                    font-weight: 700;
                    line-height: 0.48rem;
                    color: rgba(147, 153, 159, 0.9);
                    text-decoration: line-through;
                }
            }
            div{
                position: absolute;
                right: 0.36rem;
                top:0;
                input{
                    color: white;
                    font-size: 0.2rem;
                    line-height: 0.48rem;
                    outline: none;
                    width: 1.48rem;
                    height: 0.48rem;
                    background-color:rgb(0,160,220);
                    border-radius: 0.24rem;
                }
                
            }
           
            
        }
        .sellerName {
            p {
                font-size: 0.28rem;
                color: rgb(7, 17, 27);
                line-height: 0.26rem;
                margin:0.36rem auto 0.16rem 0.36rem;
                font-weight: 700;
                line-height: 0.28rem;
            }
            .starCount {
                display: flex;
                .num{
                    margin: auto 0.24rem auto 0.36rem;
                    font-size: 0.2rem;
                    color: rgb(147, 153, 159);
                    line-height: 0.26rem;
                }
                .ratings{
                    font-size: 0.2rem;
                    color: rgb(147, 153, 159);
                    line-height: 0.26rem;
                }
            }
            .introduce{
                font-size: 0.24rem;
                line-height: 0.48rem;
                margin-left: 0.52rem;
                color:rgb(77,85,93);
                border-radius: 0.24rem;
                font-weight: 200;
                box-sizing: border-box;
                margin-right: 0.52rem;
            }
        }
        
      }
    }
    // 空div
    .myDiv {
      width: 100%;
      height: 0.35rem;
      border-bottom: 0.02rem solid rgba(147, 153, 159, 0.2);
      box-sizing: border-box;
      background-color: rgba(147, 153, 159, 0.1);
    }
     // button
    .goodword{
        font-size: 0.28rem;
        font-weight: 500;
        margin:0.36rem auto auto 0.36rem;
    }
   
    .comment {
        width: 100%;
        // commentDetail
        .commentDetail {
            display: flex;
            margin: 0.36rem 0.36rem auto 0.36rem;
            border-bottom: 0.01rem solid rgba(7,17,27,0.2);
            box-sizing: border-box;
            height: 2.24rem;
            position:relative;
        .userImg {
            img {
            width: 0.56rem;
            height: 0.56rem;
            border-radius: 0.5rem;
            }
            margin-right: 0.24rem;
            box-sizing: border-box;
        }
    // userInfo star
        .userInfo {
            .infoTop {
            display: flex;
            margin-bottom: 0.08rem;
            .userName {
                font-size: 0.2rem;
                color: rgb(7, 17, 27);
                line-height: 0.24rem;
                font-weight: 200;
            }
            .commentDate {
                font-size: 0.2rem;
                position:absolute;
                color: rgb(147, 153, 159);
                line-height: 0.24rem;
                font-weight: 200;
                margin-left:3.5rem;
            }
        }
        // star
        .starTime{
            display: flex;
            margin-bottom: 0.12rem;
            .star {
                img {
                    width: 0.2rem;
                    height: 0.2rem;
                }
            }
            .arriveTime {
                font-size: 0.2rem;
                color: rgb(147, 153, 159);
                line-height: 0.24rem;
                font-weight: 200;
                margin:0.1rem auto 0.12rem 0.12rem;
                margin-left:0.1rem;
                box-sizing: border-box;
            }
        }
        .commentContent {
            font-size: 0.24rem;
            color:rgb(7,17,27);
            // line-height: 0.36rem;
            width: 5.9rem;
            height: 0.6rem;
            overflow: hidden;
            text-overflow: ellipsis;
        }
        }
        .foodName{
            width:4.6rem;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            justify-content: space-around;
            // border:1px solid black;
            font-size: 0.18rem;
            color:rgb(147,153,159);
            img{
                width: 0.5rem;
                height: 0.4rem;
            }
            div{
                width: 1.25rem;
                display:inline-block;
                height: 0.33rem;
                border:0.01rem solid rgba(7,17,27,0.2);
                border-radius: 0.02rem;
                background-color: rgba(255,255,255,0.2);
                margin-top:0.05rem;
                margin-right:0.1rem;
                text-align: center;
                line-height: 0.33rem;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
        }
        }
    }
  }
}
</style>




